html, body {
  font-family: "Gill Sans", sans-serif;
  font-size: 16px;
  color: #0f0f0f;
  width: 100%;
  min-height: 100%;
  margin: 0;
  padding: 0; }

* {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

.clearfix {
  clear: both; }

.header {
  width: 1200px;
  height: 45px;
  line-height: 45px;
  margin: 50px auto 40px; }
  .header__logo {
    display: inline-block;
    width: 113px;
    height: 43px;
    background-image: url("../images/logo.png");
    background-repeat: no-repeat;
    margin-right: 140px; }
  .header__nav {
    display: inline-block;
    vertical-align: top; }
  .header__link {
    font-size: 18px;
    font-weight: 600;
    color: #0f0f0f;
    text-decoration: none;
    margin-right: 50px;
    -moz-transition: color 0.45s ease 0s;
    -o-transition: color 0.45s ease 0s;
    -webkit-transition: color 0.45s ease 0s;
    transition: color 0.45s ease 0s; }
    .header__link:hover {
      color: #999999; }
  .header__socials {
    display: inline-block;
    float: right; }
  .header__social {
    display: inline-block;
    width: 45px;
    height: 45px;
    border: 2px solid #D5D5D5;
    background-repeat: no-repeat;
    background-position: center;
    -moz-transition: border-color 0.45s ease 0s;
    -o-transition: border-color 0.45s ease 0s;
    -webkit-transition: border-color 0.45s ease 0s;
    transition: border-color 0.45s ease 0s; }
    .header__social:hover {
      border-color: #999999; }
    .header__social--facebook {
      background-image: url("../images/facebook.png"); }
    .header__social--instagram {
      background-image: url("../images/instagram.png"); }
    .header__social--twitter {
      background-image: url("../images/twitter.png"); }

.home {
  width: 1200px;
  height: 700px;
  margin: 0 auto;
  position: relative; }
  .home__map {
    opacity: 0;
    width: 600px;
    height: 600px;
    background-repeat: no-repeat;
    background-position: center;
    position: absolute;
    left: 50%;
    -moz-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    -o-transform: translateX(-50%);
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    -moz-transition: opacity 0.7s ease 0s;
    -o-transition: opacity 0.7s ease 0s;
    -webkit-transition: opacity 0.7s ease 0s;
    transition: opacity 0.7s ease 0s; }
    .home__map--gray {
      background-image: url("../images/map--gray.png"); }
    .home__map--color {
      background-image: url("../images/map--color.png"); }
    .home__map--visible {
      opacity: 1; }
  .home__controls {
    position: absolute;
    top: 150px;
    width: 100%;
    text-align: center;
    opacity: 1;
    -moz-transition: all 0.4s ease-in 0s;
    -o-transition: all 0.4s ease-in 0s;
    -webkit-transition: all 0.4s ease-in 0s;
    transition: all 0.4s ease-in 0s; }
    .home__controls--hidden {
      opacity: 0;
      top: 200px; }
  .home__heading {
    font-size: 140px;
    font-weight: bold;
    font-style: italic;
    margin: 0; }
  .home__subheading {
    font-size: 28px;
    font-weight: 400;
    font-style: italic;
    margin: 0; }
  .home__actions {
    width: 470px;
    height: 55px;
    margin: 32px auto 155px; }
  .home__action {
    float: left;
    display: inline-block;
    width: 230px;
    height: 55px;
    line-height: 55px;
    font-size: 18px;
    font-weight: 600;
    text-transform: uppercase;
    text-align: center;
    text-decoration: none;
    color: #0f0f0f;
    cursor: pointer;
    border: 1px solid #0f0f0f;
    -moz-transition: all 0.45s ease 0s;
    -o-transition: all 0.45s ease 0s;
    -webkit-transition: all 0.45s ease 0s;
    transition: all 0.45s ease 0s; }
    .home__action:hover {
      color: #fff;
      background: #ff4a8f;
      border-color: #ff4a8f; }
    .home__action--pink {
      border: none;
      color: #fff;
      background: #ff307f;
      margin-left: 10px; }
    .home__action--close {
      position: absolute;
      bottom: 0;
      right: 0;
      -moz-transition: opacity 2s ease-out 0s;
      -o-transition: opacity 2s ease-out 0s;
      -webkit-transition: opacity 2s ease-out 0s;
      transition: opacity 2s ease-out 0s; }
    .home__action--hidden {
      opacity: 0; }
  .home__down {
    display: block;
    width: 25px;
    height: 31px;
    margin: 0 auto;
    background-image: url("../images/arrow.png"); }
